import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material';
import { PostItComponent } from '../post-it/post-it.component';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../data.service';
import { PostIt } from '../postIt';
import { DriehoekData } from '../driehoekData';


@Component({
  selector: 'app-driehoek',
  templateUrl: './driehoek.component.html',
  styleUrls: ['./driehoek.component.css']
})
export class DriehoekComponent implements OnInit {

  data: DriehoekData;
  school: string;
  titel: string;
  id: string;

  constructor(private dialog: MatDialog, private route: ActivatedRoute, private dataService: DataService) {
    this.school = this.route.snapshot.paramMap.get("school");
    this.id = this.route.snapshot.paramMap.get("id");
    this.titel = this.route.snapshot.paramMap.get("titel");
  }

  ngOnInit() {
    this.data = this.dataService.getDataDriehoek(this.school, this.id);
    // console.log(this.data);
    // let element = document.getElementById('content');
    // console.log(element);
    // switch(this.id){
    //   case '1':element.style.backgroundImage = '../../assets/driehoek_blauw.png';break;
    //   case '2':element.style.backgroundImage = '../../assets/driehoek_groen.png';break;
    //   case '3':element.style.backgroundImage = '../../assets/driehoek_geel.png';break;
    //   case '4':element.style.backgroundImage = '../../assets/driehoek_rood.png';break;
    // }
  }

  showPostIt(postIt) {
    const dialogRef = this.dialog.open(PostItComponent, {
      width: '75%',
      height: '75%',
      data: postIt
    });
    dialogRef.afterClosed().subscribe((res: any) => {
    });
  }
}
