export class DataService {

  constructor() { }
  public getDataDriehoek(school: string, id: string): DriehoekData {

    // switch(school){
    //   case "Het Nest": return this.getDataHetNest(id);
    //   case "Het Anker": return this.getDataHetAnker(id);
    //   case "De Cirkel": return this.getDataDeCirkel(id);
    //   case "Het Kwadrant": return this.getDataHetKwadrant(id);
    // }
    switch(school){
      case "Het Nest": return this.getDataHetNest(id);
      case "Het Anker": return this.getDataHetNest(id);
      case "De Cirkel": return this.getDataHetNest(id);
      case "Het Kwadrant": return this.getDataHetNest(id);
    }
  }

  public getDataHetNest(id: string): DriehoekData{
    let data: DriehoekData = new DriehoekData();
    let postIt1 = new PostIt("Pdf 1","https://piet-laureyns.github.io/leerlingenbegeleiding/assets/HetNestDriehoek1/pdf1.pdf");
    let postIt2 = new PostIt("doc 1","https://piet-laureyns.github.io/leerlingenbegeleiding/assets/HetNestDriehoek1/doc1.docx");
    let postIt3 = new PostIt("Ppt 1","https://piet-laureyns.github.io/leerlingenbegeleiding/assets/HetNestDriehoek1/ppt1.pptx");
    let postIt4 = new PostIt("Pdf 4","https://piet-laureyns.github.io/leerlingenbegeleiding/assets/HetNestDriehoek1/pdf4.pdf");
    switch(id){
      case '1': {
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);

        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt3);

        data.postItsTop.push(postIt2);
        data.postItsTop.push(postIt4);
      };break;
      case '2': {
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);

        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt3);

        data.postItsTop.push(postIt1);
        data.postItsTop.push(postIt2);
      };break;
      case '3': {
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt1);
        data.postItsBottom.push(postIt2);
        data.postItsBottom.push(postIt2);
        data.postItsBottom.push(postIt3);
        data.postItsBottom.push(postIt3);
        data.postItsBottom.push(postIt4);
        data.postItsBottom.push(postIt4);
        data.postItsBottom.push(postIt1);

        data.postItsMiddle.push(postIt2);
        data.postItsMiddle.push(postIt4);
        data.postItsMiddle.push(postIt4);
        data.postItsMiddle.push(postIt4);
        data.postItsMiddle.push(postIt4);

        data.postItsTop.push(postIt1);
      };break;
      case '4': {
        data.postItsBottom.push(postIt1);

        data.postItsMiddle.push(postIt2);

        data.postItsTop.push(postIt2);
      };break;
    }

    return data;
  }

  public getDataHetAnker(id: string): DriehoekData{
    return null;
  }

  public getDataDeCirkel(id: string): DriehoekData{
    return null;
  }

  public getDataHetKwadrant(id: string): DriehoekData{
    return null;
  }


}
